// taken from https://github.com/h5bp/html5-boilerplate/blob/v7.3.0/dist/css/main.css

/* ==========================================================================
   Print styles.
   Inlined to avoid the additional HTTP request:
   https://www.phpied.com/delay-loading-your-print-css/
   ========================================================================== */

@media print {
  *,
  *:before,
  *:after {
    background: transparent !important;
    color: #000 !important;
    /* Black prints faster */
    box-shadow: none !important;
    text-shadow: none !important;
  }
  a,
  a:visited {
    text-decoration: underline;
  }
  a[href]:after {
    content: " (" attr(href) ")";
  }
  abbr[title]:after {
    content: " (" attr(title) ")";
  }
  /*
     * Don't show links that are fragment identifiers,
     * or use the `javascript:` pseudo protocol
     */
  a[href^="#"]:after,
  a[href^="javascript:"]:after {
    content: "";
  }
  pre {
    white-space: pre-wrap !important;
  }
  pre,
  blockquote {
    border: 1px solid #999;
    page-break-inside: avoid;
  }
  /*
     * Printing Tables:
     * https://web.archive.org/web/20180815150934/http://css-discuss.incutio.com/wiki/Printing_Tables
     */
  thead {
    display: table-header-group;
  }
  tr,
  img {
    page-break-inside: avoid;
  }
  p,
  h2,
  h3 {
    orphans: 3;
    widows: 3;
  }
  h2,
  h3 {
    page-break-after: avoid;
  }
}

// this bullframe.css specific

@media print {
  // adjust the grid system making the layout always fixed

  body,
  .bf-container {
    min-width: 992px !important;
  }

  // a few fixes for '_utility_tables.scss'
  //
  // a <table> with a responsive container (.table-responsive) is cut
  // horizontally because there an overflow-x is applied
  // let's re-set the overflow of that container on 'auto'
  // and let the <table> element flows without a fixed/flexible with

  .bf-table-responsive {
    overflow: auto;
  }

  .bf-table-responsive table {
    width: auto;
  }

  .bf-table-responsive table,
  .bf-table {
    border-collapse: collapse !important;
  }
}
